.footer {

	padding-bottom: rem(50);
	
	@include min1050 {
		padding-bottom: rem(40);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	.logo {
		margin-right: rem(147);

		@include min1200 {
			margin-right: rem(75);
		}

		@include min {
			margin-right: 0;
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: rem(34);

			display: flex;
			justify-content: center;

			img {
				max-width: rem(220);
			}
		}
	}

	&__nav {
		flex: 1;

		ul {
			max-height: rem(64);

			display: flex;
			flex-wrap: wrap;
			flex-direction: column;

			@include min {
				max-height: 100%;
			}

			li {

				margin-bottom: rem(20);

				@include min1050 {
					text-align: center;
				}

				&:nth-child(2n) {
					margin-bottom: 0;

					@include min1050 {
						margin-bottom: rem(20);
					}
				}

				a {
					font-size: rem(15);
					color: var(--c-text-gray);

					&:hover {
						color: white;
					}
				}
			}

			li.last {
				display: none;

				@include min1050 {
					display: block;

					a {
						color: white;
						font-weight: 500;
					}
				}

				@include min {
					display: none;
				}
			}
		}
	}

	&__right {
		display: flex;
		flex-direction: column;

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: rem(35);

			flex-direction: row-reverse;
			justify-content: space-between;
		}

		@include min {
			flex-direction: column;
			margin-top: rem(20);
			justify-content: center;
			align-items: center;
		}

		.first {
			font-size: rem(15);
			color: white;
			font-weight: 500;
			margin-bottom: rem(18);

			@include min1050 {
				display: none;
			}

			@include min {
				display: block;
			}

			&:hover {
				color: var(--c-yellow);
			}
		}

		.center {
			font-size: rem(13);
			color: var(--c-text-gray);
			margin-bottom: rem(14);

			span {
				color: var(--c-yellow);
			}
		}

		.last {
			font-size: rem(13);
			color: var(--c-text-gray);
		}
	}

}