.modal_new {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 49;

	background-color: var(--c-bg);
	display: none;
	overflow-y: auto;


	// display: flex;
	// align-items: center;
	// justify-content: center;
	
	.container {
		margin-top: rem(165);
		margin-bottom: rem(195);

		display: flex;

		@include min540 {
			margin: 0;
		}

	}

	&__close {
		position: absolute;
		top: rem(40);
		right: rem(40);
		z-index: 2;

		@include min540 {
			top: rem(24);
			right: rem(15);
		}
	}

	&__wrap {
		box-shadow: 0px 0px 50px #8D8BA7;
		border-radius: rem(20);
		overflow: hidden;
		width: 100%;

		@include min540 {
			border-radius: 0;
		}
		

		&_header {
			position: relative;
			height: rem(272);
			width: 100%;
			background-size: cover;

			padding: rem(80) rem(88) rem(50) rem(80);

			@include min540 {
				padding: rem(224) rem(15) 0 rem(15);
				background-position: top center;
			}

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(100);
				background: linear-gradient(0deg, #18172A 0%, rgba(24, 23, 42, 0) 100%);
			}

			.title {
				position: relative;
				z-index: 2;
				font-size: rem(40);
				color: white;
				font-weight: 500;
				line-height: 150%;
				max-width: rem(634);

				margin-bottom: rem(12);

				@include min540 {
					font-size: rem(30);
					max-width: 100%;
				}
			}

			.date {
				position: relative;
				z-index: 2;
				display: flex;
				align-items: center;

				.icon {
					margin-right: rem(10);
				}

				span {
					color: var(--c-text-gray);
					line-height: 150%;
				}
			}
		}

		&_content {
			padding: rem(70) rem(88) rem(100) rem(88);

			@include min540 {
				padding: 0 rem(15) rem(70) rem(15);
				margin-top: rem(170);
			}

			p {
				color: var(--c-text-gray-light);
				line-height: 150%;
			}
		}

		&_content.suffle {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			p {
				max-width: rem(349);
				margin-bottom: rem(38);
			}
		}
	}
}

.modal_new.active {
	display: block;
}