.header {
	// background-color: var(--c-bg);

	padding-bottom: rem(225);

	&__top {
		padding-top: rem(57);
		padding-bottom: rem(135);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@include min1050 {
			padding-top: rem(45);
			padding-bottom: rem(100);
		}

		@include min540 {
			padding-top: rem(25);
			padding-bottom: rem(45);
		}

		.logo {
			@include min1050 {
				margin-right: rem(42);
			}

			@include min540 {
				max-width: rem(72);
			}
		}

		&_nav {

			@include min991 {
				display: none;
			}

			ul {
				display: flex;
				align-items: center;

				li {
					margin-right: rem(67);

					transition: var(--transition);

					@include min1050 {
						margin-right: rem(40);
					}
					

					&:last-child {
						margin-right: 0;
					}

					&:hover {
						transform: translateY(-5px);
						
						a {
							color: var(--c-yellow);
						}
					}

					a {
						color: var(--c-text-gray-light);

						@include min1050 {
							font-size: rem(15);
						}
					}
				}
			}
		}

		&__right {
			@include min1050 {
				margin-left: rem(42);

				.button_yellow_border {
					display: none;
				}
			}

			.burger {
				display: none;

				@include min991 {
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					width: rem(40);
					height: rem(20);

					span {
						width: 100%;
						height: rem(1.5);
						background-color: var(--c-yellow);
					}
				}
			}
			
		}
	}

	&__bottom {
		.swiper {
			// margin-bottom: rem(85);

			@include min991 {
				margin-bottom: rem(80);
			}

			.swiper-slide {
				display: flex;
				justify-content: space-between;
				// align-items: center;

				@include min991 {
					flex-wrap: wrap;
				}
			}
		}

		&_left {
			display: flex;
			flex-direction: column;

			flex: 0 0 rem(473);
			max-width: rem(473);

			@include min1050 {
				flex: 0 0 rem(445);
				max-width: rem(445);
			}

			@include min991 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: rem(44);
			}

			.title_h1 {
				// font-size: rem(60);
				// font-weight: 800;
				color: var(--c-text-gray-light);
				margin-bottom: rem(20);

				@include min991 {
					text-align: center;
					margin-bottom: rem(15);
				}
			}

			.text {
				color: var(--c-text-gray);
				line-height: 150%;
				margin-bottom: rem(40);

				@include min1050 {
					font-size: rem(15);
					margin-bottom: rem(30);
				}

				@include min991 {
					text-align: center;
				}

				span {
					color: var(--c-text-gray-light);
				}
			}

			.button_yellow {
				align-self: flex-start;

				@include min991 {
					margin-left: auto;
					margin-right: auto;
				}

				@include min540 {
					width: 100%;
				}
			}
		}

		&_right {
			position: relative;

			@include min991 {
				margin-left: auto;
				margin-right: auto;
			}
			
			// &::before {
			// 	content: "";
			// 	position: absolute;
			// 	bottom: 0;
			// 	left: 0;
			// 	width: 100%;
			// 	height: rem(89);
			// 	border-radius: rem(85);

			// 	background-color: #050414;
			// 	filter: blur(50px);
			// }
		}

		&_nav {
			display: flex;
			justify-content: center;
		}
	}
}