.section_news {
	padding-top: rem(225);

	padding-bottom: rem(225);

	@include min768 {
		padding-top: rem(165);
		padding-bottom: rem(120);
	}

	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(80);

		@include min768 {
			margin-bottom: rem(40);
		}

		.page_smart {
			display: none;

			@include min768 {
				display: flex;

				.first, .last {
					color: var(--c-text-gray);
				}

				.last {
					margin-left: rem(2);
					&::before {
						content: "/";
					}
				}
			}
		}
	}

	.title_h2 {
		color: var(--c-text-gray-light);
		
	}

	.swiper {
		margin-bottom: rem(56);

		.swiper-slide {
			display: flex;
			height: auto;
		}


	}

	&__nav {
		display: flex;
		justify-content: center;

		@include min540 {
			display: none;
		}
	}
}

.new_item {
	display: flex;
	flex-direction: column;

	overflow: hidden;

	background-color: #211F3D;
	border-top-left-radius: var(--ui-border-4);
	border-top-right-radius: var(--ui-border-4);

	@include min540 {
		background-color: transparent;
	}

	.js--text-buffer {
		display: none;
	}

	&__image {
		height: rem(255);
		margin-bottom: rem(34);

		@include min540 {
			height: rem(165);
			margin-bottom: rem(24);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;

		padding-left: rem(30);
		padding-right: rem(30);
		padding-bottom: rem(34);

		height: 100%;

		@include min540 {
			padding: 0;
		}

		&_box {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.show_full {
				display: none;

				@include min540 {
					display: block;

					font-weight: 500;
					color: var(--c-yellow);
				}
			}
		}
	}

	&__title {
		font-size: rem(20);
		color: white;
		font-weight: 500;
		margin-bottom: rem(20);

		@include min540 {
			font-size: rem(17);
			margin-bottom: rem(10);
		}
	}

	&__text {
		color: var(--c-text-gray-light);
		line-height: 150%;
		margin-bottom: rem(34);

		@include min540 {
			margin-bottom: rem(24);
			font-size: rem(15);
		}

		button {
			font-weight: bold;
			color: var(--c-yellow);

			@include min540 {
				display: none;
			}
		}
	}

	&__time {
		margin-top: auto;
		display: flex;
		align-items: center;

		.icon {
			margin-right: rem(12);
		}

		span {
			color: var(--c-text-gray);
			line-height: 150%;
		}
	}
}