.modal_review {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(24, 23, 42, .7);
    display: none;
    align-items: center;
    justify-content: center;
	z-index: 49;

	@include min540 {
		
	}
	
    &__close {
        position: absolute;
        top: rem(30);
        right: rem(30);

		@include min540 {
			top: rem(30);
			right: rem(15);
		}
    }

    &__wrap {
        position: relative;
        background-color: white;
        border-radius: rem(20);
        padding: rem(60) rem(42);
        display: flex;
        flex-direction: column;
        width: rem(498);

		@include min540 {
			width: 100%;
			height: 100%;
			border-radius: 0;

			padding: rem(80) rem(15);
		}
    }
    &__header {
        display: flex;
		align-items: center;
        // flex-wrap: wrap;
        margin-bottom: rem(50);

		@include min540 {
			margin-bottom: rem(42);
			flex-direction: column;
		}

        &_image {
			flex: 0 0 rem(80);
            width: rem(80);
            height: rem(80);
            border-radius: 50%;
            border: rem(1.5) solid var(--c-yellow);
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: rem(20);

			@include min540 {
				margin-right: 0;
				margin-bottom: rem(20);
			}
        }
        &_text {
            display: flex;
            flex-direction: column;
            .title {
                color: var(--c-black);
                font-weight: 500;
                margin-bottom: rem(7);
				word-break: break-all;
				max-width: rem(240);

				@include min540 {
					max-width: 100%;
					margin-bottom: rem(5);
					text-align: center;
				}
            }
            .date {
                font-size: rem(15);
                color: var(--c-text-gray);

				@include min540 {
					text-align: center;
				}
            }
        }
    }
    &__text {
        p {
            color: var(--c-black);
            font-size: rem(15);
            line-height: 150%;
        }
    }
}

.modal_review.active {
	display: flex;
}