:root {
	--f-default: 'Gotham', sans-serif;

	--c-bg: #18172B;
	--c-black: #2D2B49;

	--c-text-gray: #B2B1BE;
	--c-blue-accent: #2F23F4;
	--c-text-dark: #18172A;
	--c-text-gray-light: #E0DEF4;
	--c-yellow: #F3CF89;
	--c-pink: #EB00FF;

	--transition: 250ms ease;

	--ui-border-4: 4px;
}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
	font-size: inherit;
}

a {
	text-decoration: none;
}

a, button {
	transition: var(--transition);

	* {
		transition: var(--transition);
	}
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: var(--f-default);
	font-size: rem(17);
	font-weight: normal;
	// height: 100%;
	background-color: var(--c-bg);
	min-width: 320px;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.relative {
	position: relative;
}

.container {
	max-width: 1280px;
	margin: 0 auto;

	@include min1280 {
		margin-left: rem(50);
		margin-right: rem(50);
	}

	@include min768 {
		margin-left: rem(15);
		margin-right: rem(15);
	}
}

.container-max {
	max-width: 1600px;
	margin: 0 auto;

	@include min1440 {
		max-width: 1280px;
	}
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.overflow-hidden {
	overflow: hidden;
}

.pgn {
	li {
		margin-bottom: rem(15);
		a {
			color: white;
		}
	}
}