.section_clip_white {
	position: relative;
	background-color: white;

	&::before {
		content: "";
		position: absolute;
		top: rem(-100);
		left: -25px;
		width: calc(100% + 50px);
		height: rem(200);
		background-color: white;
		// border-radius: 100%;
		border-radius: 80% 80%;

		@include min1050 {
			top: rem(-60);
			height: rem(120);
		}
	}

	&::after {
		content: "";
		position: absolute;
		bottom: rem(-100);
		left: -25px;
		width: calc(100% + 50px);
		height: rem(200);
		background-color: white;
		// border-radius: 100%;
		// clip-path: ellipse(50% 49% at 50% 50%);
		border-radius: 80% 80%;

		@include min1050 {
			bottom: rem(-60);
			height: rem(120);
		}
	}
	
}