.section_search {
	padding: rem(150) 0;

	@include min1050 {
		padding: rem(60) 0;
	}

	.title_h2 {
		margin-bottom: rem(80);
		color: var(--c-text-dark);

		@include min768 {
			text-align: center;
			margin-bottom: rem(40);
		}
	}

	&__form {
		display: flex;
		flex-direction: column;

		.field_set {
			position: relative;
			width: 100%;
			max-width: rem(846);
			margin-left: auto;
			margin-right: auto;

			margin-bottom: rem(30);

			&__field {
				width: 100%;
				height: rem(70);

				display: flex;
				align-items: center;

				border-radius: var(--ui-border-4);

				border: rem(2) solid var(--c-yellow);
				padding: 0 rem(100) 0 rem(44);

				color: var(--c-text-gray);
				font-size: rem(17);

				@include min768 {
					height: rem(56);
					padding: 0 rem(20);
				}

				@include placeholder {
					color: var(--c-text-gray);
				}
			}

			&__button {
				position: absolute;
				top: 0;
				right: 0;
				
				width: rem(70);
				height: rem(70);
				background-color: var(--c-text-dark);
				border-radius: var(--ui-border-4);

				display: flex;
				align-items: center;
				justify-content: center;

				@include min768 {
					display: none;
				}

				svg {
					path {
						fill: white;
					}
				}
			}
		}

		.abc_list {
			width: 100%;
			max-width: rem(876);

			margin-left: auto;
			margin-right: auto;

			margin-bottom: rem(106);

			@include min540 {
				margin-bottom: rem(20);
			}

			ul {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				@include min540 {
					justify-content: flex-start;
				}

				li {

					@include min540 {
						margin-right: rem(15);
						margin-bottom: rem(20);

						// &:last-child {
						// 	margin-right: 0;
						// }
					}

					a {
						position: relative;
						font-size: rem(20);
						font-weight: 500;
						color: var(--c-text-dark);

						@include min540 {
							font-size: rem(15);
						}

						&::before {
							content: "";
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							height: rem(1);
							transition: var(--transition);
							opacity: 0;
							background-color: var(--c-text-dark);
						}

						&:hover {
							&::before {
								opacity: 1;
							}
						}
					}

					a.active {
						font-weight: 800;
						&::before {
							opacity: 1;
						}
					}
				}
			}
		}

		.result_table {
			display: flex;
			flex-direction: column;

			&__head {
				margin-bottom: rem(38);

				@include min540 {
					margin-bottom: rem(30);
				}

				b {
					font-size: rem(35);
					color: var(--c-text-dark);

					@include min540 {
						font-size: rem(25);
					}
				}
			}

			&__list {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				margin-left: auto;
				margin-right: auto;

				max-width: rem(990);
				width: 100%;

				@include min540 {
					flex-direction: column;
					justify-content: flex-start;
				}

				.list_show {
					display: none;

					@include min540 {
						display: block;
						text-align: center;
						margin-top: rem(40);
						font-weight: 500;
						color: var(--c-text-dark);
					}
				}

				.list {

					flex: 1;

					&:last-child {
						margin-left: rem(220);

						@include min540 {
							margin-left: 0;

							display: none;
						}
					}

					li {
						line-height: 182%;

						a {
							color: black;

							&:hover {
								text-decoration: underline;
							}
						}
					}
				}

				.list.show {
					display: block;
				}
			}
		}
	}

}