.swiper_control {
	display: flex;
	// flex-wrap: wrap;
	align-items: center;

	&__left,
	&__right {
		flex: 0 0 rem(58);
		width: rem(58);
		height: rem(58);
		border-radius: var(--ui-border-4);
		border: rem(1.5) solid var(--c-black);

		display: flex;
		align-items: center;
		justify-content: center;

		@include min540 {
			display: none;
		}

		&:hover {
			background-color: var(--c-black);
		}

		svg {
			path {
				fill: var(--c-text-gray-light);
			}
		}
	}

	.swiper-pagination-bullets-dynamic {
		width: rem(200) !important;

		display: flex;
		justify-content: center;

		.swiper-pagination-bullet {
			left: 0 !important;
			flex: 0 0 rem(3) !important;
			width: rem(3) !important;
			height: rem(3) !important;

			transition: var(--transition);

			// &:nth-child(4) {
			// 	width: auto !important;
			// 	height: auto !important;
			// }

			// &:nth-child(5) {
			// 	width: auto !important;
			// 	height: auto !important;
			// }
		}

		.swiper-pagination-bullet-active,
		.swiper-pagination-bullet-active-next,
		.swiper-pagination-bullet-active-next-next,
		.swiper-pagination-bullet-active-prev,
		.swiper-pagination-bullet-active-prev-prev {
			flex: 0 0 rem(10) !important;
			width: rem(10) !important;
			height: rem(10) !important;
		}

		.swiper-pagination-bullet-active-next-next,
		.swiper-pagination-bullet-active-prev-prev {
			flex: 0 0 rem(7) !important;
			width: rem(7) !important;
			height: rem(7) !important;
		}
	}

	&__bullet {
		margin: 0 rem(50);

		display: flex;
		align-items: center;

		

		.swiper-pagination-bullet {
			flex: 0 0 rem(10);
			width: rem(10);
			height: rem(10);
			background-color: var(--c-black);
			opacity: 1;
			margin: 0 rem(5);

			transition: var(--transition);

			@include min540 {
				margin: 0 rem(10);
			}

			&:hover {
				background-color: var(--c-yellow);
			}

			&:nth-child(4) {
				width: rem(7);
				height: rem(7);
			}

			&:nth-child(5) {
				width: rem(5);
				height: rem(5);
			}
		}

		

		

		.swiper-pagination-bullet-active {
			background-color: var(--c-yellow);
		}
	}
}

.swiper_control_light {
	.swiper_control__left,
	.swiper_control__right {
		border: rem(1.5) solid var(--c-yellow);

		&:hover {
			background-color: var(--c-yellow);

			svg {
				path {
					fill: white;
				}
			}
		}

		svg {
			path {
				fill: var(--c-yellow);
			}
		}
	}

	.swiper_control__bullet {
		.swiper-pagination-bullet {
			background-color: var(--c-text-gray);

			&:hover {
				background-color: var(--c-yellow);
			}
		}

		.swiper-pagination-bullet-active {
			background-color: var(--c-yellow);
		}
	}
}