.section_reviews {
	padding-top: rem(125);
	padding-bottom: rem(125);

	margin-bottom: rem(150);

	@include min540 {
		padding-top: rem(75);
		padding-bottom: rem(10);
	}

	.container {
		
	}

	.container-max {
		position: relative;

		&::before,
		&::after {
			pointer-events: none;
			position: absolute;
			content: "";
			top: 0;
			width: rem(330);
			height: rem(320);

			z-index: 2;

			@include min1050 {
				display: none;
			}
		}

		&::before {
			left: 0;
			// background: rgb(255, 255, 255);
			background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 70%, rgba(255, 255, 255, 0) 100%);
		}

		&::after {
			right: 0;
			// background: rgb(255, 255, 255);
			background: linear-gradient(270deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 70%, rgba(255, 255, 255, 0) 100%);
		}
	}

	

	.title_h2 {
		color: var(--c-text-dark);
		margin-bottom: rem(150);

		@include min1050 {
			margin-bottom: rem(77);
		}

		@include min540 {
			margin-bottom: rem(50);

			span {
				display: none;
			}
		}
	}

	.swiper {
		// height: rem(400);

		// @include min1050 {
		// 	height: auto;
		// }
		margin-bottom: rem(86);

		@include min1050 {
			margin-bottom: rem(80);
		}

		@include min768 {
			
		}

		.swiper-wrapper {}

		.swiper-slide {
			height: auto;

			@include min768 {
				margin-bottom: rem(15);
			}
		}
	}
}

.review_item {
	position: relative;
	height: rem(110);
	border-radius: var(--ui-border-4);
	margin-left: rem(55);
	background-color: #EFEFEF;

	@include min540 {
		margin-left: 0;
	}

	.js--text-buffer {
		display: none;
	}

	&__image {
		position: absolute;
		top: 0;
		left: rem(-55);

		width: rem(110);
		height: rem(110);
		border-radius: 50%;
		background-color: white;
		border: rem(1.5) solid var(--c-yellow);

		display: flex;
		align-items: center;
		justify-content: center;

		@include min540 {
			display: none;
		}
		
	}

	&__right {
		padding: rem(20) rem(23) rem(14) rem(75);
		height: 100%;
		display: flex;
		flex-direction: column;

		@include min540 {
			padding: rem(15);
		}

		.text {
			font-size: rem(15);
			line-height: 150%;
			color: var(--c-text-dark);

			button {
				font-weight: bold;
				position: relative;
				z-index: 3;

				@include min540 {
					display: none;
				}
			}
		}

		.flex {
			margin-top: auto;

			.left, .right {
				font-size: rem(15);
				color: var(--c-text-gray);
			}

			.left {
				font-weight: 500;
			}
		}
	}
}