.modal_overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 100%;
  background-color: var(--c-bg);
  display: none;
  padding: 2.625rem 3.125rem;
}

@media (max-width: 991px) {
  .modal_overlay {
    flex-direction: column;
  }
}

.modal_overlay__close {
  position: absolute;
  top: 2.625rem;
  right: 3.125rem;
}

.modal_overlay__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6rem;
}

.modal_overlay__header_profie {
  width: 3.375rem;
  height: 3.375rem;
  border-radius: 50%;
}

.modal_overlay__header .button_yellow_border {
  position: absolute;
  top: 2.625rem;
  left: 3.125rem;
  width: 18.125rem;
  justify-content: center;
}

@media (max-width: 540px) {
  .modal_overlay__header .button_yellow_border {
    top: auto;
    bottom: 3.125rem;
    width: calc(100% - 84px);
  }
}

.modal_overlay__nav {
  margin-left: auto;
  margin-right: auto;
  margin-top: 12rem;
}

@media (max-width: 540px) {
