}

.button_yellow_border {
  padding: 0 2.1875rem;
  height: 3.375rem;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  border: 0.09375rem solid var(--c-yellow);
}

.button_yellow_border span {
  font-weight: bold;
  color: var(--c-text-gray-light);
}

.button_yellow_border .arrow {
  margin-left: 0.46875rem;
}

.button_yellow_border .arrow svg path {
  fill: var(--c-text-gray-light);
}

.button_yellow {
  padding: 0 2.1875rem;
  height: 3.375rem;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--c-yellow);
}

.button_yellow span {
  color: var(--c-text-dark);
  font-weight: 500;
}

.section_clip_white {
  position: relative;
  background-color: white;
}

.section_clip_white::before {
  content: "";
  position: absolute;
  top: -6.25rem;
  left: -25px;
  width: calc(100% + 50px);
  height: 12.5rem;
  background-color: white;
  border-radius: 80% 80%;
}

@media (max-width: 1050px) {
  .section_clip_white::before {
    top: -3.75rem;
    height: 7.5rem;
  }
}

.section_clip_white::after {
  content: "";
  position: absolute;
  bottom: -6.25rem;
  left: -25px;
  width: calc(100% + 50px);
  height: 12.5rem;
  background-color: white;
  border-radius: 80% 80%;
}

@media (max-width: 1050px) {
  .section_clip_white::after {
    bottom: -3.75rem;
    height: 7.5rem;
  }
}

.swiper_control {
  display: flex;
  align-items: center;
}

.swiper_control__left, .swiper_control__right {
  flex: 0 0 3.625rem;
  width: 3.625rem;
  height: 3.625rem;
  border-radius: var(--ui-border-4);
  border: 0.09375rem solid var(--c-black);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 540px) {
  .swiper_control__left, .swiper_control__right {
    display: none;
  }
}

.swiper_control__left:hover, .swiper_control__right:hover {
  background-color: var(--c-black);
}

.swiper_control__left svg path, .swiper_control__right svg path {
