.modal_overlay {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	
	width: 100%;
	height: 100%;
	background-color: var(--c-bg);
	display: none;

	padding: rem(42) rem(50);

	@include min991 {
		// display: flex;
		flex-direction: column;
	}

	&__close {
		position: absolute;
		top: rem(42);
		right: rem(50);
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(96);

		&_profie {
			width: rem(54);
			height: rem(54);
			border-radius: 50%;
		}

		.button_yellow_border {
			position: absolute;
			top: rem(42);
			left: rem(50);
			width: rem(290);
			justify-content: center;

			@include min540 {
				// width: 100%;

				top: auto;
				bottom: rem(50);
				width: calc(100% - 84px);
			}
		}
	}

	&__nav {
		margin-left: auto;
		margin-right: auto;
		margin-top: rem(192);

		@include min540 {
			margin-top: rem(120);
		}

		ul {
			li {
				text-align: center;
				margin-bottom: rem(40);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					color: var(--c-text-gray-light);
					font-size: rem(20);
					font-weight: 500;
				}
			}
		}
	}

}

.modal_overlay.active {
	@include min991 {
		display: flex;
	}
}